<template>
  <div>
    <el-card class="goods">
      <div class="goods-top">
        <!--  -->
        <el-input placeholder="" v-model="search" size="small" clearable @clear="getGoodsList">
          <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
        </el-input>
        <el-button type="primary" size="small" @click="showAddGoods"></el-button>
      </div>
      <!--  -->
      <el-table ref="goodslist" :data="goodsList.slice((currentPage - 1) * pageSize, currentPage * pageSize)" stripe>
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="" prop="goodsName"></el-table-column>
        <el-table-column label="" prop="tag"></el-table-column>
        <el-table-column label="" prop="price" width="60"></el-table-column>
        <el-table-column label="" prop="salesNum"></el-table-column>
        <el-table-column label="" width="160">
          <template slot-scope="scope">
            {{scope.row.putAwayDate | transformTime}}
          </template>
        </el-table-column>
        <el-table-column label="" prop="stock"></el-table-column>
        <el-table-column label="">
          <el-button type="primary" size="mini"></el-button>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addGoodsVisible" width="36%">
      <el-form :model="goodsInfo" class="goods-add">
        <el-form-item label="">
          <el-input v-model="goodsInfo.goodsName" size="small"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="goodsInfo.tag" placeholder="" size="small">
            <el-option label="" value="shiyongyou"></el-option>
            <el-option label="" value="mianfen"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="goodsInfo.price" size="small"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="goodsInfo.salesNum" size="small"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="goodsInfo.stock" size="small"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="addGoodsVisible = false"> </el-button>
        <el-button type="primary" @click="addGoodsVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from '../../../api/index'
export default {
  data() {
    return {
      goodsList: [],
      search: '',
      total: 0,
      currentPage: 1,
      pageSize: 10,
      addGoodsVisible: false,
      goodsInfo: {
        goodsName: '',
        tag: '',
        price: '',
        salesNum: '',
        stock: '',
      },
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList() {
      const gList = await this.$axios.get(`${api.defultUrl}/goods`)
      if (gList.data.meta.status !== 200)
        return this.$message.error('')
      this.goodsList = gList.data.data
      this.total = gList.data.data.length
    },
    // 
    handleSizeChange(size) {
      this.pageSize = size
    },
    // 
    handleCurrentChange(page) {
      this.currentPage = page
    },
    showAddGoods() {
      this.addGoodsVisible = true
    },
  },
}
</script>

