.goods {
  .goods-top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 18px;
  }
  .el-input {
    width: 340px;
  }
}
.el-pagination {
  margin-top: 16px;
  display: flex;
  justify-content: flex-start;
