                <el-select v-model="editData.sex" placeholder="" size="small">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label=":">
                <el-select v-model="editData.status" placeholder="" size="small">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
