        <el-form ref="form" :model="addData" label-width="80px" class="editForm">
          <el-row>
            <el-col :span="12">
              <el-form-item label=":">
                <el-input v-model="addData.username" size="small"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label=":">
                <el-input v-model="addData.mobile" size="small"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label=":">
                <el-select v-model="addData.sex" placeholder="" size="small">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label=":">
                <el-select v-model="addData.status" placeholder="" size="small">
