<template>
  <div>
    <div class="tab">
      <el-tabs v-model="logActiveName">
        <el-tab-pane label="" name="first">
          <el-card>
            <div class="account-card">
              <!--  -->
              <span style="font-size: 14px; margin-right: 6px;">: </span>
              <el-date-picker v-model="datePickerValue" type="datetimerange" :picker-options="pickerOptions" range-separator="" start-placeholder="" end-placeholder="" align="right" size="mini" @change="logTime" value-format="timestamp">
              </el-date-picker>
              <el-button size="mini" type="primary" @click="searchLog"></el-button>
              <!--  -->
            </div>
            <!--  -->
            <div>
              <el-table :data="logDate" style="width: 100%">
                <el-table-column prop="operate_account" label="" width="420"></el-table-column>
                <el-table-column prop="operate_module" label="" width="380"></el-table-column>
                <el-table-column prop="operate_time" label="">
                  <template slot-scope="scope">
                    {{scope.row.operate_time | transformTime}}
                  </template>
                </el-table-column>
                <el-table-column prop="operate_ip" label="IP"></el-table-column>
                <el-table-column prop="operate_content" label=""></el-table-column>
              </el-table>
            </div>
            <!--  -->
            <div class="pagination">
              <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="logQuery.pagenum" :page-sizes="[10, 20, 30, 40, 60]" :page-size="logQuery.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
              </el-pagination>
            </div>
          </el-card>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import api from '../../../api/index'
export default {
  data() {
    return {
      logActiveName: 'first',
      logDate: [],
      logQuery: {
        query: '',
        pagenum: 1,
        pagesize: 10,
      },
      total: 0,
      datePickerValue: '',
      pickerOptions: {
        shortcuts: [
          {
            text: '',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
    }
  },
  created() {
    this.getProcessLog()
  },
  methods: {
    // 
    handleSizeChange(size) {
      this.logQuery.pagesize = size
      this.getProcessLog()
    },
    // 
    handleCurrentChange(num) {
      this.logQuery.pagenum = num
      this.getProcessLog()
    },
    // 
    async getProcessLog() {
      const { data: res } = await this.$axios.get(
        `${api.localUrl}/getProcessLog`,
        {
          params: this.logQuery,
        }
      )
      if (res.status !== 200)
        return this.$message.error('')
      this.logDate = res.data
      this.total = res.total
    },
    logTime(val) {
      if (val == null) {
        this.logQuery.query = ''
        this.getProcessLog()
      }
    },
    // 
    searchLog() {
      this.logQuery.query = this.datePickerValue
      this.getProcessLog()
    },
  },
}
</script>

