<template>
  <div>
    <el-card>
      <div class="order-top">
        <div>
          <el-select v-model="customerQuery.value" placeholder="" size="small" @change="getType">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
          <el-input placeholder="" v-model="customerQuery.query" size="small" clearable @clear="getCustomerList">
          </el-input>
          <el-button type="primary" size="small" icon="el-icon-search" @click="getCustomerList"></el-button>
        </div>
        <el-button type="primary" size="small" @click="showAddCustomer"></el-button>
      </div>
      <!-- table -->
      <el-table :data="customerList" @select="getSele">
        <el-table-column type="selection"></el-table-column>
        <el-table-column prop="customerName" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            {{scope.row.becomeTime | transformTime}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            {{scope.row.consumeTime | transformTime}}
          </template>
        </el-table-column>
        <el-table-column prop="customerBalance" label=""></el-table-column>
        <el-table-column prop="consumeNum" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button class="button-w" type="primary" @click="showCustomerDialog(scope.row)" size="mini"></el-button>
            <el-popconfirm confirm-button-text='' cancel-button-text='' icon="el-icon-info" icon-color="red" title="" @confirm="removeCustomer(scope.row)">
              <el-button slot="reference" type="danger" size="mini"></el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="customerQuery.pagenum" :page-sizes="[10, 20, 30, 40, 60]" :page-size="customerQuery.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!-- dialog -->
    <MinorDialog :visibleFlag.sync="add.visibleFlagAdd" :title="add.title" :width="add.width" :cradWidthHeight="add.cradWidthHeight" :showCard="add.showCard">
      <el-form slot="four" :model="addQuery" label-width="84px" size="mini" class="add-form" :rules="rules" ref="ruleFormCustomer">
        <el-form-item label=":" prop="customerName">
          <el-input v-model="addQuery.customerName"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="mobile">
          <el-input v-model="addQuery.mobile"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="channel">
          <el-select v-model="addQuery.channel" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="H5" value="H5"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="birthday">
          <el-date-picker v-model="addQuery.birthday" type="datetime" placeholder="" value-format="timestamp">
          </el-date-picker>
        </el-form-item>
        <div class="add-button">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button type="primary" size="small" @click="AddCustomer"></el-button>
        </div>
      </el-form>
    </MinorDialog>
    <!-- dialog -->
    <MinorDialog :visibleFlag.sync="details.visibleFlag" :title="details.title" :width="details.width" :cradWidthHeight="details.cradWidthHeight" :showCard="details.showCard" @clearConsumerInfo="clearConsumerInfo">
      <div slot="three" class="details-top">
        <el-image :src="url"></el-image>
        <el-row>
          <el-col :span="8">: {{customerDetails.customerName}}</el-col>
          <el-col :span="8">:{{customerDetails.customerId}}</el-col>
          <el-col :span="8">: {{customerDetails.becomeTime | transformTime}}</el-col>
          <el-col :span="8">: {{customerDetails.channel}}</el-col>
          <el-col :span="24" class="add-tag">
            <el-input size="mini" v-model="tagInputValue" v-if="tagInputVisible" @keyup.enter.native="(event)=>event.target.blur()" @blur="handleInputConfirm" ref="tagInput"></el-input>
            <el-button size="mini" v-else @click="showTagInput"></el-button>
          </el-col>
        </el-row>
      </div>
      <div slot="four" class="details-tags">
        <span>: </span>
        <el-tag v-for="(tag, index) in tags" :key="index" closable size="mini" effect="plain" @close="removeTag(tag)">
          {{tag}}
        </el-tag>
      </div>
    </MinorDialog>
  </div>
</template>

<script>
import MinorDialog from '../../components/M-Dialog.vue'
import api from '../../../api/index'
import _ from 'lodash'
import {saveProcesslog} from '../../plugins/processlog'
export default {
  components: {
    MinorDialog: MinorDialog,
  },
  data() {
    return {
      add: {
        visibleFlagAdd: false,
        title: '',
        width: '460px',
        cradWidthHeight: 'width: 100%;',
        showCard: {
          one: false,
          two: false,
          three: false,
          four: true,
        },
      },
      details: {
        visibleFlag: false,
        title: '',
        width: '70%',
        cradWidthHeight: 'width: 100%;',
        showCard: {
          one: false,
          two: false,
          three: true,
          four: true,
        },
      },
      options: [
        {
          value: 'mobile',
          label: '',
        },
        {
          value: 'customerName',
          label: '',
        },
      ],
      customerQuery: {
        query: '',
        pagenum: 1,
        pagesize: 10,
        value: 'mobile',
      },
      customerList: [],
      total: 0,
      addQuery: {
        customerName: '',
        mobile: '',
        birthday: '',
        channel: '',
        becomeTime: '',
      },
      rules: {
        customerName: [
          { required: true, message: '', trigger: 'blur' },
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { min: 11, max: 11, message: '11', trigger: 'blur' },
        ],
        channel: [
          { required: true, message: '', trigger: 'change' },
        ],
        birthday: [
          {
            type: 'date',
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      url: 'https://thirdwx.qlogo.cn/mmopen/vi_32/DYAIOgq83erS60B294sfRw7UGn7GKSnYkmUDhjO2BkVWrwDlicbpqgdwibC5DrSYLjechT03FzYC9cDBwiarDcQaw/132',
      customerDetails: [],
      tags: [],
      tagInputVisible: false,
      tagInputValue: '',
    }
  },

  created() {
    this.getCustomerList()
  },
  methods: {
    closeDialog() {
      this.add.visibleFlagAdd = false
      this.$refs.ruleFormCustomer.resetFields()
    },
    showDialog() {
      this.visibleFlag = true
    },
    getType(val) {
      this.customerQuery.value = val
    },
    // 
    async getCustomerList() {
      const { data: res } = await this.$axios.get(
        `${api.localUrl}/customerlist`,
        {
          params: this.customerQuery,
        }
      )
      if (res.status !== 200) return this.$message.error(',')
      this.customerList = res.data
      this.total = res.total
    },
    showCustomerDialog(customer) {
      this.details.visibleFlag = true
      this.customerDetails = customer
      if (customer.tag) {
        this.tags = customer.tag.split(',')
      } else {
        this.tags = []
      }
    },
    handleSizeChange(size) {
      this.customerQuery.pagesize = size
      this.getCustomerList()
    },
    handleCurrentChange(curr) {
      this.customerQuery.pagenum = curr
      this.getCustomerList()
    },
    getSele(val) {
    },
    // 
    showAddCustomer() {
      this.add.visibleFlagAdd = true
    },
    // 
    AddCustomer() {
      this.$refs.ruleFormCustomer.validate(async (vaild) => {
        if (!vaild) return this.$message.error('')
        let becomeTime = new Date().getTime()
        this.addQuery.becomeTime = becomeTime.toString().slice(0, 10)
        const { data: res } = await this.$axios.post(
          `${api.localUrl}/customerlist`,
          this.addQuery
        )
        if (res.status !== 200) return this.$message.error('')
        // 
        const addCModule = ''
        const addCContent = `"${this.addQuery.customerName}"`
        saveProcesslog(addCModule, addCContent)
        this.$message.success('')
        this.add.visibleFlagAdd = false
        this.getCustomerList()
        // 
        this.$refs.ruleFormCustomer.resetFields()
      })
    },
    // 
    async removeCustomer(id) {
      const { data: res } = await this.$axios.delete(
        `${api.localUrl}/customerlist/${id.customerId}`
      )
      if (res.status !== 200) return this.$message.error('')
      const addCModule = ''
      const addCContent = `"${id.customerName}"`
      saveProcesslog(addCModule, addCContent)
      this.getCustomerList()
    },
    clearConsumerInfo(val) {
      this.customerDetails = val
      this.tags = val
      this.getCustomerList()
    },
    // tag
    showTagInput() {
      this.tagInputVisible = true
      this.$nextTick(() => {
        this.$refs.tagInput.focus()
      })
    },
    async handleInputConfirm() {
      this.tagInputVisible = false
      if (this.tagInputValue === '' || this.tagInputValue === null) return
      let tagg = _.cloneDeep(this.tags)
      if (tagg === '' && tagg === null) {
        tagg = []
      }
      tagg.push(this.tagInputValue)
      tagg = tagg.toString()
      const { data: res } = await this.$axios.put(
        `${api.localUrl}/customerlist/${this.customerDetails.customerId}`,
        { tag: tagg }
      )
      if (res.status !== 200) return this.$message.error('tag')
      this.tags.push(this.tagInputValue)
      this.tagInputValue = ''
    },
    // 
    async removeTag(tag) {
      let tags = this.tags.filter((item) => {
        return item !== tag
      })
      tags = tags.toString()
      const { data: res } = await this.$axios.put(
        `${api.localUrl}/customerlist/${this.customerDetails.customerId}`,
        { tag: tags }
      )
      if (res.status !== 200) return this.$message.error('tag')
      this.tags = tags.split(',')
    },
  },
}
</script>

